// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import $axios from './utils/axios-reset';
import store from './store';


import { Lazyload , Swipe, SwipeItem ,Tabbar, TabbarItem , Icon , Tag , NavBar } from 'vant';

import '@/assets/libs/vant.css'
import '@/assets/libs/base.less'


// let loadingImg = require('./assets/swipe-loading.png');
// options 为可选参数，无则不传
Vue.use(Lazyload).use(Swipe).use(SwipeItem).use(Tabbar).use(TabbarItem).use(Icon).use(Tag).use(NavBar);

//设置axios
Vue.use($axios);

if(process.env.NODE_ENV == 'development'){
  console.log('开发者模式!');
  Vue.config.devtools = true; //处于开发模式,开启vuex调试
}

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  components: { App },
  template: '<App/>'
})



