import Vue from 'vue'
import Router from 'vue-router'

const IndexComponent = resolve => {
  //Indicator.open();
  require.ensure(['@/views/index/Index'],() => {
    resolve(require(/* webpackChunkName: "index" */'@/views/index/Index'))
    //Indicator.close();
  })
}
const GroupComponent = resolve => {
  //Indicator.open();
  require.ensure(['@/views/group/Index'],() => {
    resolve(require(/* webpackChunkName: "category" */'@/views/group/Index'))
    //Indicator.close();
  })
}
const CartComponent = resolve => {
  //Indicator.open();
  require.ensure(['@/views/cart/Index'],() => {
    resolve(require(/* webpackChunkName: "category" */'@/views/cart/Index'))
    //Indicator.close();
  })
}
const UserComponent = resolve => {
  //Indicator.open();
  require.ensure(['@/views/user/Index'],() => {
    resolve(require(/* webpackChunkName: "category" */'@/views/user/Index'))
    //Indicator.close();
  })
}

const NotFoundComponent = () => import('@/views/error/Error');

Vue.use(Router)

const routes = [
  {
    path:'/',
    redirect:'/index'
  },
  {
    path: '/index',
    name: 'index',
    component: IndexComponent,
    meta: {
      title: '首页-DD订酒',
      active:0,
      showBottomTabbar:true
    }
  },
  {
    path: '/group',
    name: 'group',
    component: GroupComponent,
    meta: {
      title: '商品分组-DD订酒',
      active:1,
      showBottomTabbar:true
    }
  },
  {
    path: '/cart',
    name: 'cart',
    component: CartComponent,
    meta: {
      title: '购物车-DD订酒',
      active:2,
      showBottomTabbar:true
    }
  },
  {
    path: '/user',
    name: 'user',
    component: UserComponent,
    meta: {
      title: '个人中心-DD订酒',
      active:3,
      showBottomTabbar:true
    }
  },
  {
    path: '*',
    component: NotFoundComponent
  },
];

// routes.forEach(route => {
//   route.path = route.path || '/' + (route.name || '')
// });

const router = new Router({
  mode:'history',
  routes:routes
})

router.beforeEach((to,from,next) =>{
  const title = to.meta && to.meta.title;
  if(title){
    document.title = title;
  }
  next();
});

export default router;



